@import "../variables";

.ag-theme-alpine {
    font-family: mat-font-family($custom-typography);
    .ag-root-wrapper {
        border-color: white;
    }
    .ag-row {
        transition: transform 0.8s, top 0.8s, height 0.8s, background-color 0.1s, opacity 0.2s;
    }
    .ag-cell {
        &.epop-planning-grid-cell {
            padding: 0;
            border: 0;
            border-right: 1px solid $color-lighter-grey;
            background: $color-white;
        }
    }
    
}