@import "variables";
@import "dimensions";

// MUST BE IMPORTED ONLY ONE TIME IN PROJECT

// Include the common styles for Angular Material. We include this here so that you only
// have to load a single css file for Angular Material in your app.
// Be sure that you only ever include this mixin once!
@include mat-core();

// Create the theme object. A theme consists of configurations for individual
// theming systems such as "color" or "typography" defined in variables.
$theme: mat-light-theme($palette-primary, $palette-accent, $palette-warn);

// Define a custom typography config that overrides the font-family as well as the
// `headlines` and `body-1` levels.
$custom-typography: mat-typography-config(
    $font-family: $primary-font + ', "Helvetica Neue", sans-serif',
    $body-1: mat-typography-level(1.1rem, 1.5rem, 500, $letter-spacing: 0.1rem),
    $subheading-2:
        mat-typography-level(1.1rem, 1.5rem, 500, $letter-spacing: 0.1rem),
);
// Override typography for all Angular Material, including mat-base-typography and all components.
@include angular-material-typography($custom-typography);

// Include theme styles for core and each component used in your app.
// Alternatively, you can import and @include the theme mixins for each component
// that you are using.
@include angular-material-theme($theme);

// Font styles :
@import "../assets/fonts/CooperHewitt/stylesheet.css";

/* External libs style */

// Ag grid styles: 
@import "../../node_modules/ag-grid-community/dist/styles/ag-grid.css";
@import "../../node_modules/ag-grid-community/dist/styles/ag-theme-alpine.css";
// Custom theming :
@import "theming/aggrid";


// Angular calendar
@import "../../node_modules/angular-calendar/css/angular-calendar.css";


/* Overriden styles */

// MatMenu
.epop-header-bar-user-dialog {
    .mat-menu-content:not(:empty),
    .mat-list-base {
        padding: 0;
    }
}

.epop-dialog-pane {
    &--big-corner {
        mat-dialog-container.mat-dialog-container {
            border-radius: 10px;
        }
    }
}

mat-dialog-container.mat-dialog-container {
    padding: 0;
    overflow: hidden;
    
}

// Forms
mat-radio-group {
    display: inline-block;
}

.mat-form-field, mat-radio-group {
    margin-bottom: 15px;
}
.mat-radio-button label {
    height: 30px;
}

label {
    height: 45px;
}

// MatTooltip
.mat-tooltip-panel .mat-tooltip {
    background-color: rgba(0,0,0,0.6);
    margin: 4px;
}

.mat-card:not([class*="mat-elevation-z"]) {
    box-shadow: 0 1rem 4rem -1rem rgba(0,0,0,0.15);
}

// MatTabs
.mat-tab-list {
    .mat-tab-label {
        height: $mat-tab-height;
        &-active {
            color: $color-black;
            opacity: 1;
        }
    }
}
.epop-mat-tab__no-header {
    .mat-tab-header {
        display: none;
    }
}

// Custom ePOP theming :
@import "theming/epop";