@import "src/styles/variables";
// Epop styles:

// Buttons :
a.epop_action_link {
    color: $color-primary;
    font-size: 1rem;
    line-height: 1.4rem;
    text-decoration: underline;
    text-align: left;
    cursor: pointer;
    font-weight: 500;
}

// Text styles :

.epop_uppercase {
    text-transform: uppercase;
    font-size: 1.1rem;
    letter-spacing: 0.1rem;
}

.epop_icon-button.mat-icon {
    display: flex;
    width: 16px;
    height: 16px;
    cursor: pointer;
    &:hover {
        color: $color-primary;
    }
}