@import "~@angular/material/theming";

// Palettes generated with http://mcg.mbitson.com
// This generator is was used as it reuse the main color as the color of 500.
// Some values has been modified after generation to respect our official theme colors.
// Official list can be found here : https://ftp.ideative.io/id/HUG/ePOP/colors.html

$primary: (
    50: #63ddff,
    100: #4ad8ff,
    200: #30d2ff,
    300: #17cdff,
    400: #00c6fc,
    500: #00B2E3,
    600: #009ec9,
    700: #0092BA,
    800: #007696,
    900: #00627d,
    A100: #7de3ff,
    A200: #96e8ff,
    A400: #b0eeff,
    A700: #004e63,
    contrast: (
        50: #000000,
        100: #000000,
        200: #000000,
        300: #000000,
        400: #000000,
        500: #ffffff,
        600: #ffffff,
        700: #ffffff,
        800: #ffffff,
        900: #ffffff,
        A100: #000000,
        A200: #000000,
        A400: #000000,
        A700: #000000,
    ),
);

$warn: (
    50: #ffbd68,
    100: #ffb24f,
    200: #ffa735,
    300: #ff9c1c,
    400: #ff9102,
    500: #E88300,
    600: #ce7500,
    700: #b56600,
    800: #9b5800,
    900: #824900,
    A100: #ffc982,
    A200: #ffd49b,
    A400: #ffdfb5,
    A700: #683b00,
    contrast: (
        50: #000000,
        100: #000000,
        200: #000000,
        300: #000000,
        400: #000000,
        500: #ffffff,
        600: #ffffff,
        700: #ffffff,
        800: #ffffff,
        900: #ffffff,
        A100: #000000,
        A200: #000000,
        A400: #000000,
        A700: #000000,
    ),
);

$accent: (
    50: #014334,
    100: #015d48,
    200: #01765c,
    300: #018f6f,
    400: #02a883,
    500: #02c296,
    600: #02f4be,
    700: #35B295,
    800: #2cfdce,
    900: #46fdd4,
    A100: #02f4be,
    A200: #02DBAA,
    A400: #02c296,
    A700: #5ffeda,
    contrast: (
        50: #000000,
        100: #000000,
        200: #000000,
        300: #000000,
        400: #000000,
        500: #ffffff,
        600: #ffffff,
        700: #ffffff,
        800: #ffffff,
        900: #ffffff,
        A100: #000000,
        A200: #000000,
        A400: #000000,
        A700: #000000,
    ),
);

// Definition of MD palettes :
$palette-primary: mat-palette($primary);
$palette-accent: mat-palette($accent);
$palette-warn: mat-palette($warn);

// Finally, definition of application colors :

// Primary colors (blue)
$color-primary: mat-color($palette-primary);
$color-primary-dark: mat-color($palette-primary, 700);

// Accent colors (green) 
$color-accent: mat-color($palette-accent);
$color-accent-light: mat-color($palette-accent, 400);
$color-accent-dark: mat-color($palette-accent, 700);

// Warn colors (orange)
$color-warn: mat-color($palette-warn);

// Danger colors (red)
$color-danger: #EB303F;
$color-danger-dark: #EB303F;

// Other colors
$color-black: #000000;
$color-darker-grey: #555555;
$color-dark-grey: #7C7C7C;
$color-medium-grey: #b3b3b3;
$color-lighter-grey: #EBEBEB;
$color-ultra-light-grey: #F7F7F7;
$color-ultra-grey: #F8F8F8;
$color-white:  #ffffff;
$ultra-blue: #DEF8FF;
$color-purple: #C055FF;


// Fonts :
$primary-font: "Cooper Hewitt";
$secondary-font: "Cooper Hewitt Book";